let Toast = {};
Toast.install = function (Vue, options) {
    Vue.myGlobalMethod = function () {  // 1. : vue-custom-element
        // ...
    }
    Vue.directive('my-directive', {  // 2. // vue-touch
        bind (el, binding, vnode, oldVnode) {
            // ...
        }
        //...
    })
    Vue.mixin({
        created: function () {  // 3.  mixin: vuex
            // ...
        }
        //...
    })
    Vue.prototype.$myMethod = function (options) {  // 4.  Vue.prototype 
        // ...
    }
    Vue.prototype.$toastMsg = 'Hello Vue Plugin';
    // msgtypeoption
    Vue.prototype.$toast = function (msg, option, type) {
        let toastTpl = Vue.extend({
            template: '<div class="vue-toast toast-'+type+'">' + msg + '</div>'
        });
        let {keys, values, entries} = Object;
        let tpl = new toastTpl().$mount().$el;
        for (let [key, value] of entries(option)) {
            tpl.setAttribute(key, value)
            tpl.style[key] = value
        }
        document.body.appendChild(tpl);
        setTimeout(function () {        // 42.5
            document.body.removeChild(tpl);
        }, 2500)
    }

}

export default Toast


